<template>
  <div>
    <el-cascader :options="areaList"
      :props="cityProps" filterable separator="-"
     placeholder="请选择地区支持输入搜索" v-model="areas"
      @change="getAreas" ref="area"></el-cascader>
  </div>
</template>

<script>
export default {
  name: 'cmsArea',
  props: {
    data: {
      type: Array,
      default: function() {
        return []
      }
    }
  },
  data() {
    return {
      cityProps: {
        value: 'areaCode',
        label: 'areaName',
        children: 'childs'
      },
      areas: [] // 选中的地区
    }
  },
  computed: {
    areaList() {
      return this.$store.state.dict.area
    }
  },
  watch: {
    data: {
      handler() {
        this.areas = this.$cms.deepCopy(this.data)
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    getAreas(value) {
      const data = []
      const names = this.$refs.area.currentLabels
      const ids = value
      console.log(ids)
      ids.forEach((element, index) => {
        const parms = {
          code: element,
          areaName: names[index]
        }
        data.push(parms)
      })
      this.$emit('change', data)
    }
  },
  created() {

  }
}
</script>

<style>
</style>



