<template>
  <div class="cms-list-layout" v-loading="loading">
    <cms-prompt collapse="true">
      <li>发货或收货地址中各只能同时存在一个默认地址</li>
      <li>发货或收货地址各最多只能添加20条地址</li>
      <li>默认地址在系统或平台需要处理卖家信息时会使用，请谨慎设置</li>
    </cms-prompt>
    <div class="max-warp">
      <h3 class="cms-segmentation">发货地址
        <el-button size="mini" type="primary" @click="setAddressInfo('新增发货地址',1,'save')">新增</el-button>
      </h3>
      <el-table :data="deliveryList" style="width: 100%" stripe>
        <el-table-column prop="customerName" label="联系人">
          <template slot-scope="scope">
            <div class="text-overflow">
              {{scope.row.customerName}}
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="tel" label="联系电话"></el-table-column>
        <el-table-column label="地址">
          <template slot-scope="scope">
            <div
              class="text-overflow"
            >{{scope.row.provinceName}}{{scope.row.cityName}}{{scope.row.areaName}}{{scope.row.address}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="isDefault" label="是否默认">
          <template slot-scope="scope">
            <cms-switch
              v-model="scope.row.isDefault"
              :id="scope.row.id"
              @change="deliveryToggleDefault"
            ></cms-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="175" fixed="right" align="center">
          <div slot-scope="scope">
            <el-button
              type="text"
            @click="setAddressInfo('修改发货地址',1,'update',scope.row)"
            >编辑</el-button>
            <el-button
              type="text"
              @click.stop="deleteData(scope.row.id)"
              v-if="!scope.row.isDefault"
            >删除</el-button>
          </div>
        </el-table-column>
      </el-table>
    </div>
    <div class="max-warp">
      <h3 class="cms-segmentation">收货地址
        <el-button size="mini" type="primary" @click="setAddressInfo('新增收货地址',2,'save')">新增</el-button>
      </h3>
      <el-table :data="shippingList" style="width: 100%" stripe>
        <el-table-column prop="customerName" label="联系人"></el-table-column>
        <el-table-column prop="tel" label="联系电话"></el-table-column>
        <el-table-column label="地址">
          <template slot-scope="scope">
            <div
              class="text-overflow"
            >{{scope.row.provinceName}}{{scope.row.cityName}}{{scope.row.areaName}}{{scope.row.address}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="isDefault" label="是否默认">
          <template slot-scope="scope">
            <cms-switch
              v-model="scope.row.isDefault"
              :id="scope.row.id"
              @change="shippingToggleDefault"
            ></cms-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="175" fixed="right" align="center">
          <div slot-scope="scope">
            <el-button
              type="text"
                @click="setAddressInfo('修改收货地址',2,'update',scope.row)"
            >编辑</el-button>
            <el-button
              type="text"
              @click.stop="deleteData(scope.row.id)"
              v-if="!scope.row.isDefault"
            >删除</el-button>
          </div>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog :title="addrInfo" :visible="addrVisable" :before-close="handleClose" width="650px">
      <div style="padding:15px">  
      <el-form :model="addressForm" label-width="110px" ref="form" :rules="rules">
        <el-form-item label="公司名称">
          <el-input v-model="addressForm.companyName" maxlength="150"></el-input>
        </el-form-item>
        <el-form-item label="联系人姓名" prop="customerName">
          <el-input v-model="addressForm.customerName" maxlength="30"></el-input>
        </el-form-item>
        <el-form-item label="所在地" prop="areaCode">
          <cms-area :data="areaData" @change="getArea"></cms-area>
        </el-form-item>
        <el-form-item label="详细地址" prop="address">
          <el-input v-model="addressForm.address" maxlength="150"></el-input>
        </el-form-item>
        <el-form-item label="联系电话" prop="tel">
          <el-input v-model="addressForm.tel" maxlength="13"></el-input>
        </el-form-item>
        <el-form-item label="邮编">
          <el-input v-model="addressForm.postCode" maxlength="13"></el-input>
        </el-form-item>
        <el-form-item label="是否默认" prop="isDefault">
          <cms-switch v-model="addressForm.isDefault"></cms-switch>
        </el-form-item>
      </el-form>
       </div>
      <div slot="footer">
         <el-button type="primary" @click="submitAddr(addressType)" size="small">保存</el-button>
          <el-button type="info" @click="handleClose" size="small">取消</el-button>
      </div>
     
     
    </el-dialog>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  storeAddressPage,
  storeAddressSave,
  storeAddressUpdate,
  storeAddressDelete,
  setStoreAddressDefault,
  setStoreAddressNotDefault
} from './../../../api/shop/order.js'
import cmsArea from '@/components/cms/cmsArea'
import rules from '@/rules'
const defaultData = {
  companyName: '',
  customerName: '',
  addressType: '',
  provinceCode: '',
  provinceName: '',
  cityCode: '',
  cityName: '',
  areaCode: '',
  areaName: '',
  address: '',
  tel: '',
  postCode: '',
  isDefault: true
}
export default {
  name: '',
  mixins: [list],
  components: {
    cmsArea
  },
  data() {
    return {
      timer: [],
      activeStatus: '',
      shippingList: [],
      deliveryList: [],
      areaData: [],
      addressForm: Object.assign({}, defaultData),
      rules: {
        // 修改规则
        customerName: [rules.required()],
        areaCode: [rules.required('必须选择一个地区', 'change')],
        address: [rules.required('必须填写一个地址', 'blur'), rules.max(50)],
        tel: [rules.required(), rules.tel()],
        postCode: [rules.number(), rules.$length(6)],
        isDefault: [rules.required()]
      },
      addrInfo: '收货地址',
      addressType: 1,
      addrVisable: false
    }
  },
  filters: {},
  methods: {
    getArea(value) {
      this.addressForm.provinceCode = value[0].code
      this.addressForm.provinceName = value[0].areaName
      this.addressForm.cityCode = value[1].code
      this.addressForm.cityName = value[1].areaName
      this.addressForm.areaCode = value[2].code
      this.addressForm.areaName = value[2].areaName
    },
    shippingToggleDefault(value, id) {
      // 收货地址默认
      if (value) {
        setStoreAddressDefault({ id: id }).then(res => {
          if (res.code === 200) {
            this.$successMessage('操作成功')
            this.getTableData()
          }
        })
      } else {
        setStoreAddressNotDefault({ id: id }).then(res => {
          if (res.code !== 200) {
            this.$errorMessage(res.message)
            this.getTableData()
          }
        })
      }
    },
    deliveryToggleDefault(value, id) {
      // 发货地址默认
      if (value) {
        setStoreAddressDefault({ id: id }).then(res => {
          if (res.code === 200) {
            this.$successMessage('操作成功')
            this.getTableData()
          } else {
            this.$errorMessage(res.message)
          }
        })
      } else {
        setStoreAddressNotDefault({ id: id }).then(res => {
          if (res.code !== 200) {
            this.$errorMessage(res.message)
            this.getTableData()
          }
        })
      }
    },
    setAddressInfo(title, status, type, data) { // 判断表单的操作类型
      if (type === 'save') {
        this.addrInfo = title
        this.addressForm.addressType = status
        this.addressType = type
        this.addrVisable = true
      } else {
        this.addrInfo = title
        this.addressForm.addressType = status
        this.addressForm = Object.assign(this.addressForm, data)
        this.areaData = [data.provinceCode, data.cityCode, data.areaCode]
        this.addressType = type
        this.addrVisable = true
      }
    },
    submitAddr(type) {
      this.$refs.form.validate(valid => {
        if (valid) {
          if (type === 'save') {
            this.saveAddressInfo()
          } else {
            this.updateAddressInfo()
          }
        }
      })
    },
    saveAddressInfo() {
      storeAddressSave(this.addressForm).then(res => {
        if (res.code === 200) {
          this.$successMessage('新增成功')
          this.handleClose()
          this.getTableData()
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    updateAddressInfo() {
      storeAddressUpdate(this.addressForm).then(res => {
        if (res.code === 200) {
          this.$successMessage('修改成功')
          this.getTableData()
          this.handleClose()
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    deleteData(id) {
      this.$confirm('是否确认删除该地址', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(res => {
          storeAddressDelete({ ids: [id] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('删除成功')
              this.getTableData()
            } else {
              this.$errorMessage(res.message)
            }
          })
        })
        .catch(() => {})
    },
    getTableData() {
      this.loading = true
      storeAddressPage().then(res => {
        if (res.code === 200) {
          this.shippingList = res.data.filter(v => {
            return v.addressType === 2
          })
          this.deliveryList = res.data.filter(v => {
            return v.addressType === 1
          })
          this.loading = false
        }
      })
    },
    handleClose() {
      this.addressForm = Object.assign({}, defaultData)
      this.addrVisable = false
      this.$refs.form.clearValidate()
      this.addrInfo = ''
      this.areaData = []
    }
  },
  created() {
    this.getTableData()
  }
}
</script>

<style scoped lang='scss'>

</style>




